import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Layout, Menu, Row, Col } from "antd";
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  FileDoneOutlined,
  PieChartOutlined,
  AuditOutlined,
  AlertOutlined,
} from "@ant-design/icons";

import User from "../pages/admin/user/index";

import "./layout.css";

const { Header, Sider, Content } = Layout;
function getItem(label, key, icon, children) {
  return {
    key,
    icon,
    children,
    label,
  };
}

const items = [
  getItem("看板", "board", <PieChartOutlined />),
  getItem("日志管理", "log", <FileDoneOutlined />),
  getItem("消息管理", "message", <AlertOutlined />),
  getItem("内容管理", "admin", <AuditOutlined />, [
    getItem("文章管理", "artitle"),
    getItem("评论管理", "comment"),
  ]),
];

function AdminLayout(props) {
  const [collapsed, setCollapsed] = useState(false);
  const [activekey, setActivekey] = useState(["board"]);
  //   const { pathname } = useLocation();
  const navigate = useNavigate();

  const handleRouterTo = ({ keyPath }) => {
    console.log(keyPath);
    setActivekey(keyPath[0]);
    if (keyPath.length < 2) keyPath = "/admin/" + keyPath.join("");
    else keyPath = "/" + keyPath.reverse().join("/");

    navigate(keyPath);
  };

  const { children } = props;

  return (
    <Layout style={{ width: "100%", height: "100%" }}>
      <Sider trigger={null} collapsible collapsed={collapsed}>
        <div className="logo" />
        <Menu
          onClick={handleRouterTo}
          theme="dark"
          defaultSelectedKeys={["board"]}
          selectedKeys={activekey}
          mode="inline"
          items={items}
        />
      </Sider>
      <Layout className="site-layout">
        <Header
          className="site-layout-background"
          style={{
            padding: "0 30px 0 24px",
          }}
        >
          <Row>
            <Col span={12} align="left">
              {React.createElement(
                collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
                {
                  className: "trigger",
                  onClick: () => setCollapsed(!collapsed),
                }
              )}
            </Col>
            <Col span={12} align="right">
              <User />
            </Col>
          </Row>
        </Header>

        <Content
          className="site-layout-background"
          style={{
            margin: "24px 16px",
            padding: 24,
            minHeight: 280,
          }}
        >
          {children}
        </Content>
      </Layout>
    </Layout>
  );
}

export default AdminLayout;
